import React from "react";
import { Zform, ZcolorPicker } from "zerod";
import { Input, message, InputNumber, Button } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC } = HOC;
import doc from "zerod/components/Zform/doc.md";
let hasHide = false;
export default AmdDocHOC(doc, {
	demo1: function() {
		class Myjavascript extends React.PureComponent {
			items = [
				{
					key: "serviceCode",
					label: "服务编码",
					render: (form) => {
						return new Promise((resolve) => {
							setTimeout(() => {
								resolve(<Input placeholder="请输入服务编码" />);
							}, 5000);
						});
					},
					//antd的 form.getFieldDecorator的options
					options: () => ({
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					}),
				},
				{
					key: "serviceName",
					label: "服务名称",
					render: (form) => {
						return <Input placeholder="请输入服务名称" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "colorValue",
					label: "颜色值",
					render: (form) => {
						return <ZcolorPicker className="z-margin-top-4" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "serviceRemark",
					label: "服务说明",
					render: (form) => {
						return <Input.TextArea rows={2} placeholder="请输入服务说明" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "servicePort",
					label: "端口号",
					labelWidth:"80px",
					render: (form) => {
						return <InputNumber min={11110} max={65535} step={10} />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
				{
					key: "myButton",
					label: "按钮",
					render: (form, changeFormItems) => {
						return (
								<Button
								type="primary"
								onClick={() => {
									const newItems = this.items.slice(0);
									newItems.splice(4, 1);
									changeFormItems(hasHide ? this.items : newItems);
									hasHide = !hasHide;
								}}
							>
								{hasHide ? "显示" : "不显示"}端口号
							</Button>
						);
					},
				},
				{
					key: "confProperty",
					label: "默认配置",
					render: (form) => {
						return <Input.TextArea rows={15} placeholder="请输入默认配置" />;
					},
					//antd的 form.getFieldDecorator的options
					options: {
						//验证规则
						rules: [
							{
								required: true,
								message: "不能为空。",
							},
						],
					},
				},
			];
			defaultValue = {
				serviceCode: "9999",
				serviceRemark: "llll",
			};
			render() {
				return (
					<Zform
						labelLayout="horizontal"
						formDefaultValues={this.defaultValue}
						items={this.items}
						onSubmit={(values) => {
							// console.log(values);
							return Promise.resolve().then((re) => {
								message.success("提交成功：" + JSON.stringify(values));
							});
						}}
					/>
				);
			}
		}
		return <Myjavascript />;
	},
});
